.productdata{
    background-color: white;
    width: 350px;
    height: 500px;
    /* height: 450px; */
    /* border: 1px solid black; */
    margin: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 20px;
    border-radius: 20px;
    transition: all .4s ease;

    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.productdata:hover{
    transform: translate3D(0,-1px,0) scale(1.05);
    transition: all .4s ease;
    cursor: pointer;
}
.productdata img{
    width: 98%;
    margin-bottom: 20px;
}
.productdata_btn{
    /* border: 1px solid black; */

    width: 250px;
    margin: auto;
    display: flex;
    margin-top: 15px;
    justify-content: space-evenly;
    align-items: center;
    padding: 1px;
    margin-bottom: 20px;
}
.productdata_btn button{
    width: 100px;
    height: 30px;
    font-size: 1.07rem;
    cursor: pointer;
    border-radius: 5px;
    
}


@media only screen and (max-width:865px){
    .productdata{
        overflow: hidden;
        width: 300px;
        height: 400px;
        margin: 10px;
        margin-bottom: 30px;
       
    
    }
    .productdata img{
        width: 90%;
        margin-bottom: 10px;
    }
}